import firstWelcome from './images/petrchatbotscrshts/firstWelcome.png';
import karluvMost from './images/petrchatbotscrshts/karluvMost.png';
import helpWith from './images/petrchatbotscrshts/helpWith.png';
import answerMessages from './images/petrchatbotscrshts/helpWith.png';
import Welcome from './images/newScreens/Welcome.png';
import Convo from './images/newScreens/Convo.png';
import Info from './images/newScreens/Info.png';
import download from './images/app-store-badge.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import "./App.css";
import features from "./text.json";
import 'bootstrap';
import Card from "./Card.js";
import React, { useRef, useEffect } from "react";
import Rellax from "rellax";
import Navbar from 'react-bootstrap/Navbar';

let jaromirSoukup;

class Main extends React.Component {
  constructor(props){
    super(props);
    this.state = {features: features, phone: firstWelcome, imgClassesOne: "opacityOne", imgClassesTwo: "opacityZero", imgClassesThree: "opacityZero"};
  }
  componentDidMount() {
    function isInViewport(element) {
      const rect = element.getBoundingClientRect();
      return (
          rect.top >= 0 &&
          rect.left >= 0 &&
          rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
          rect.right <= (window.innerWidth || document.documentElement.clientWidth)
      );
    }
    document.addEventListener(
      "scroll", 
      function scroll(){
        this.setState({
          isOneVisible: isInViewport(document.querySelector(".animat:nth-child(1) .wrapTagline")) ? "visible" : "goaway aNameIDunno",
          isTwoVisible: isInViewport(document.querySelector(".animat:nth-child(4) .wrapTagline")) ? "visible" : "goaway aNameIDunno",
          isThreeVisible: isInViewport(document.querySelector(".animat:nth-child(7) .wrapTagline")) ? "visible" : "goaway aNameIDunno"
        })
        if(this.state.isOneVisible === "visible") {
          this.setState({
            imgClassesOne: "opacityOne",
            imgClassesTwo: "opacityZero",
            imgClassesThree: "opacityZero"
          })
        }
        if(this.state.isTwoVisible === "visible") {
          this.setState({
            imgClassesOne: "opacityZero",
            imgClassesTwo: "opacityOne",
            imgClassesThree: "opacityZero"
          })
        }
        if(this.state.isThreeVisible === "visible") {
          this.setState({
            imgClassesOne: "opacityZero",
            imgClassesTwo: "opacityZero",
            imgClassesThree: "opacityOne"
          })
        }
      }.bind(this));
  }

  render () {
  return (
    <div className="App">
      <Navbar bg="light" sticky="top">
        <Navbar.Brand href="#home" id="brand"><span className="orangeSpan"><a href="/petr"> m</a></span>onumen</Navbar.Brand>
      </Navbar>
      <p className="textDescription">
        This is a webpage for the app <span className="orangeSpan">monumen</span>.
      </p>
      <p className="textDescription">
        We developed <span className="orangeSpan">our app</span> as a part of the competition Azure Cup 2021 and our mission is to help everybody identify Prague monuments
      </p>
      <div className="scrollContainer">
      <div className="imgWrapper">
        <div className="imgCropper">
        <img src={Info} className={this.state.imgClassesThree} alt="" />
        <img src={Convo} className={this.state.imgClassesTwo} alt="" />
        <img src={Welcome} className={this.state.imgClassesOne} alt="" />
        </div>
        </div>
      <div className="mainParallax">
      {/*
      RIP a minute of silence for this sh!t 
      {
      this.state.features.features.map((item,i)=>{
        if(i%2){
          return [
            <div className="animat"></div>,
            <Card 
         text1={this.state.features.features[i].text1} 
         text2={this.state.features.features[i].text2}
         orange={this.state.features.features[i].orange}
         />
            ]
        } else {
         return [<Card 
         visibility={this.state.isOneVisible}
         text1={this.state.features.features[i].text1} 
         text2={this.state.features.features[i].text2}
         orange={this.state.features.features[i].orange}
         />,
         <div className="animat"></div>
         ]
        }
     })} */}
     <Card 
         visibility={this.state.isOneVisible}
         text1={this.state.features.features[0].text1} 
         text2={this.state.features.features[0].text2}
         orange={this.state.features.features[0].orange}
         />
         <div className="animat"></div>
         <div className="animat"></div>
      <Card 
         visibility={this.state.isTwoVisible}
         text1={this.state.features.features[1].text1} 
         text2={this.state.features.features[1].text2}
         orange={this.state.features.features[1].orange}
         />
         <div className="animat"></div>
         <div className="animat"></div>
         <Card 
         visibility={this.state.isThreeVisible}
         text1={this.state.features.features[2].text1} 
         text2={this.state.features.features[2].text2}
         orange={this.state.features.features[2].orange}
         />
      </div>
      </div>
      <div id="download">
        <h1>Coming to App Store soon</h1>
        <form>
          <input id="downloadButton" type="image" src={download} alt=""/>
        </form>
        <p className="textDescription">
          Our <a href="https://github.com/VojtechKarger/MonumentalAppAzureCup"><span className="orangeSpan">Github</span></a>
        </p>
      </div>
      <Navbar bg="light">
        <Navbar.Brand href="#home">monumen ©{new Date().getFullYear()}</Navbar.Brand>
      </Navbar>
    </div>
  );
  }
}

export default Main;


